const INITIAL_STATE = {
  transactions: null,
};

export function transactionReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case "SET_TRANSFERS":
      return {
        ...state,
        transactions: action.transactions,
      };
    case "ADD_TRANSFER":
      return {
        ...state,
        transactions: [...state.transactions, action.transaction],
      };
    default:
      return state;
  }
}
