import { transactionService } from "../../services/transactionService";

export const loadTransactions = () => {
  return async (dispatch) => {
      const transactions = await transactionService.getTransactions();
    dispatch({ type: "SET_TRANSFERS", transactions });
  };
};

export const sendCoins = (contactEmail, amount) => {
  return async (dispatch) => {
    const transaction = await transactionService.addTransaction(contactEmail, amount);
    dispatch({ type: "ADD_TRANSFER", transaction });
  };
};


