import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { TransactionFund } from "../../cmps/TransactionFund/TransactionFund";
import { setLoggedInUser } from "../../store/actions/userActions";
import { loadTransactions, sendCoins } from "../../store/actions/transactionActions";
import altContactImg from "../../assets/icons/contact.png";

import { TransactionsTable } from "../../cmps/TransactionsTable/TransactionsTable";
import NiceButton from "../../cmps/NiceButton/NiceButton";
import editBtnLogo from "../../assets/icons/edit.png";
import plusLogo from "../../assets/icons/plus-black.png";
import minusLogo from "../../assets/icons/minus.png";

export const ContactDetailsPage = (props) => {
  const loggedUser = useSelector((state) => state.userReducer.user);
  const [isAllTransactions, setIsAllTransactions] = useState(false);
  const [contact, setContact] = useState(null);
  const [transactionsToShow, setTransactionsToShow] = useState(null);
  const [userContactTransactions, setUserContactTransactions] = useState(null);
  const transactions = useSelector((state) => state.transactionReducer.transactions);
  const dispatch = useDispatch();

  const iaMoreLogo = isAllTransactions ? minusLogo : plusLogo;

  useEffect(() => {
    if (!loggedUser) dispatch(setLoggedInUser());
    else if (!transactions) dispatch(loadTransactions());
  }, [loggedUser, transactions, dispatch]);

  useEffect(() => {
    if (loggedUser) {
      const id = props.match.params.id;
      const contact = loggedUser.contacts.find((contact) => contact._id === id);
      setContact(contact);
    }
  }, [loggedUser, props.match.params.id]);

  useEffect(() => {
    if (contact && transactions) {
      const transactionByContact = transactions.filter(
        (transaction) =>
          contact.contactEmail === transaction.to ||
          contact.contactEmail === transaction.from
      );
      if (transactionByContact.length) {
        setUserContactTransactions(transactionByContact);
        if (isAllTransactions) setTransactionsToShow(transactionByContact);
        else setTransactionsToShow(transactionByContact.slice(0, 3));
      }
    }
  }, [isAllTransactions, transactions, contact]);

  const onEditContact = () => {
    props.history.push(`/contact/edit/${contact._id}`);
  };

  const onTransactionCoins = (transactionAmount, ev) => {
    ev.preventDefault();
    if (loggedUser.coins < transactionAmount) return alert("No enough money");
    dispatch(sendCoins(contact.contactEmail, transactionAmount));
  };

  const IconMoreLessBtn = () => <img src={iaMoreLogo} alt="" />;

  const IconEdit = () => <img src={editBtnLogo} alt="" />;
  return contact ? (
    <div className="contact-details-page">
      <div className="contact-details">
        <NiceButton
          Icon={IconEdit}
          onClick={onEditContact}
          className="edit-logo"
        />
        <div className="contact-img-details">
          {contact.contactImg ? (
            <img src={contact.contactImg} alt="" className="contact-img" />
          ) : (
            <img src={altContactImg} alt="" className="contact-img" />
          )}
        </div>
        <p>Name: {contact.contactName}</p>
        <p>Email: {contact.contactEmail}</p>
        <p>Phone: {contact.contactPhone}</p>
      </div>
      <TransactionFund contact={contact} onTransactionCoins={onTransactionCoins} />
      {userContactTransactions && (
        <div className="transactions-by-contact">
          {userContactTransactions.length > 3 && (
            <NiceButton
              onClick={() => setIsAllTransactions((prev) => !prev)}
              Icon={IconMoreLessBtn}
              className={"more-less-btn"}
            ></NiceButton>
          )}
          <TransactionsTable
            title="Transactions with contact"
            rows={transactionsToShow}
          />
        </div>
      )}
    </div>
  ) : (
    <div>Contact not found...</div>
  );
};
