import { httpService } from "./httpService";

export const transactionService = {
  addTransaction,
  getTransactions,
  getTransactionsByContactId,
};

async function addTransaction(contactEmail, amount) {
  return await httpService.post(`transaction/`, { contactEmail, amount });
}

async function getTransactions() {
  try {
    return await httpService.get(`transaction/`);
  } catch {}
}

async function getTransactionsByContactId(userId) {
  try {
    return await httpService.get(`transaction/${userId}`);
  } catch {}
}
